* {
    margin: 0px;
}

body {
    background: #111;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 25px;
}

.wrapper {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    color: #fff;
    text-align: center;
}

.input {
    background: #fff;
    border-radius: 10px;
    width: 300px;
    max-height: 400px;
    color: #111;
    padding: 10px;
}

.input:focus {
    outline: none;
}